import React, { useCallback, useContext, useEffect } from "react"

import cn from "classnames"
import as from "../styles/auth-styles.module.scss"

import { AUTH_FLOW_COMPONENTS, AuthContext } from "../contexts/Auth"

const EmailVerification = () => {

    const {
        authMsg,
        currentUser,
        setAuthFlowComponent,
        setAuthMsg,
        setEmailVerified
    } = useContext(AuthContext)

    const resendVerificationEmail = () => {
        if (!currentUser.emailVerified) {
            // console.log("Resending verification email")
            currentUser.sendEmailVerification()
                .then(() => recheckEmailVerification())
                .catch((error) => console.log(error))
        } else {
            console.log("email address already verified")
        }
    }

    const checkVerification = () => {
        // console.log("Rechecking verification")
        currentUser.reload()
            .then(() => {
                if (currentUser.emailVerified) {
                    setEmailVerified(true)
                    setAuthFlowComponent(AUTH_FLOW_COMPONENTS.LOGIN)
                    setAuthMsg("")
                } else {
                    recheckEmailVerification()
                }
            })
    }

    const recheckEmailVerification = useCallback(
        () => {
            if(!currentUser.emailVerified) {
                setTimeout(() => checkVerification(), 5000);
            }
        },
        [currentUser.emailVerified, checkVerification]
    )

    useEffect(
        () => {
            if (!currentUser.emailVerified) {
                // console.log("Email is not verified")
                setEmailVerified(false)
                recheckEmailVerification()
            } else {
                setEmailVerified(true)
                setAuthMsg("")
                setAuthFlowComponent(AUTH_FLOW_COMPONENTS.LOGIN)
            }
        },
        [currentUser.emailVerified, recheckEmailVerification, setAuthFlowComponent, setAuthMsg, setEmailVerified] // only run on first render
    )

    return (
        <div className={as.container}>
            <div className={as.row}>
                <h4>{authMsg}</h4>
            </div>
            <div className={as.row}>
                <p>You will arrive at your destination web page soon after clicking on the verification link.</p>
            </div>
            <div className={as.row}>
                <p>Didn't recieve a verification email? Verification link expired? Click "Resend Verification Email".</p>
            </div>
            <div className={as.row}>
                <button
                    type="button"
                    className={cn(as.btn, as['btn-primary'])}
                    onClick={resendVerificationEmail}
                >
                    Resend Verification Email
                </button>
            </div>
        </div>
    )
}

export default EmailVerification